<template>
  <b-row align-h="center" align-v="center" class="h-100">
    <b-col sm="auto">
      <b-card no-body class="m-4 shadow-sm">
        <b-card-body>
          <b-card-title title-tag="h3" class="m-5">Login</b-card-title>
          <div class="text-center my-5 mx-sm-5">
            <div
              id="google-signin-button"
              class="d-flex justify-content-center"
            ></div>
          </div>
        </b-card-body>
      </b-card>
    </b-col>
