<script>
export default {
  mounted() {
    gapi.signin2.render('google-signin-button', {
      scope: 'profile email',
      width: 240,
      height: 50,
      longtitle: true,
      theme: 'dark',
      onsuccess: this.onSignInSuccess,
      onfailure: this.onSignInFailure,
    })
  },
  methods: {
    onSignInSuccess(user) {
      const profile = user.getBasicProfile()
      console.log(profile)
    },
    onSignInFailure(...args) {
      console.error(args)
    },
  },
